/* Меню бургер */
.menu {
  display: none;
  width: 300px; }

.menu__icon {
  display: none;
  width: 45px;
  height: 35px;
  position: absolute;
  top: 20px;
  right: 40px;
  cursor: pointer; }

.menu__icon span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: #ff9966;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .15s ease-in-out; }

.menu__icon span:nth-child(1) {
  top: 0px; }

.menu__icon span:nth-child(2),
.menu__icon span:nth-child(3) {
  top: 13px; }

.menu__icon span:nth-child(4) {
  top: 26px; }

.menu.menu_state_open .menu__icon span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%; }

.menu.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg); }

.menu.menu_state_open .menu__icon span:nth-child(3) {
  transform: rotate(-45deg); }

.menu.menu_state_open .menu__icon span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%; }

header .header-top .menu.menu_state_open .menu__links {
  display: flex;
